import React, { Component } from "react";
import Input from "./Input";
import Joi from "joi-browser";

class FormMain extends Component {
  state = {
    data: {
      username: "",
      password: "",
    },
    errors: {},
  };
  schema = {
    username: Joi.string().required().label("Username"),
    password: Joi.string().required().label("Password"),
  };

  handleChange = ({ currentTarget: input }) => {
    const errors = { ...this.state.errors };
    const errorMessage = this.validateProperty(input);
    if (errorMessage) errors[input.name] = errorMessage;
    else delete errors[input.name];

    const data = { ...this.state.data };
    data[input.name] = input.value;
    this.setState({ data, errors });
  };

  validate = () => {
    const errors = {};
    const options = { abortEarly: false };
    const result = Joi.validate(this.state.data, this.schema, options);
    if (!result.error) return null;
    for (let item of result.error.details) {
      errors[item.path[0]] = item.message;
      console.log(result.error.details);
    }
    return errors;
  };

  validateProperty = ({ name, value }) => {
    const obj = { [name]: value };
    const schema = { [name]: this.schema[name] };
    const { error } = Joi.validate(obj, schema, { abortEarly: false });
    return error ? error.details[0].message : null;
  };

  handleSubmit = (e) => {
    e.preventDefault();
    const errors = this.validate();
    this.setState({ errors: errors || {} });
    if (errors) {
      return;
    }
    console.log("submitted. No errors detected");
  };
  renderButton = (label) => {
    return (
      <button
        type="submit"
        disabled={this.validate()}
        className="btn btn-primary d-flex justify-content-center ml-3"
      >
        {label}
      </button>
    );
  };
  style = {
    marginTop: "10em",
    padding: "15px",
    width: "30rem",
  };
  renderInput = (name, label) => {
    const { data, errors } = this.state;
    return (
      <Input
        type="text"
        label={label}
        name={name}
        value={data[name]}
        onChange={this.handleChange}
        error={errors[name]}
      />
    );
  };

  render() {
    return (
      <div className="main d-flex justify-content-center">
        <form onSubmit={this.handleSubmit} style={this.style}>
          {this.renderInput("username", "Username")}
          {this.renderInput("password", "Password")}
          {this.renderButton("Login")}
        </form>
      </div>
    );
  }
}

export default FormMain;
